import React, { useState } from 'react';
import PropTypes from 'prop-types';
import './Slider.scss';

const Slider = ({ type, subtype, steps, handleSize }) => {
  const [value, setValue] = useState(type === 'discrete' ? 0 : 50);
  const [range, setRange] = useState([20, 80]);

  const handleChange = (event) => {
    const newValue = type === 'discrete' ? Number(event.target.value) : Number(event.target.value);
    if (subtype === 'range') {
      // Implement logic for handling range changes here
      // For simplicity, assuming newValue is an array [min, max]
      setRange([newValue[0], newValue[1]]);
    } else {
      setValue(newValue);
    }
  };

  return (
    <div className={`slider slider-${handleSize}`}>
      <input
        type="range"
        className={`slider-input ${subtype}`}
        min="0"
        max={type === 'discrete' ? steps : 100}
        value={subtype === 'range' ? range : value}
        onChange={handleChange}
        step={type === 'discrete' ? 1 : undefined}
      />
      <div className="slider-values">
        {subtype === 'range' ? `${range[0]} - ${range[1]}` : value}
      </div>
    </div>
  );
};

Slider.propTypes = {
  type: PropTypes.oneOf(['continuous', 'discrete']),
  subtype: PropTypes.oneOf(['single', 'range']),
  steps: PropTypes.number,
  handleSize: PropTypes.oneOf(['size_24', 'size_32']),
};

Slider.defaultProps = {
  type: 'continuous',
  subtype: 'single',
  steps: 10,
  handleSize: 'size_24',
};

export default Slider;
